import React from 'react'
import { Link } from 'gatsby'
import { Button, Box, Heading, Text } from 'theme-ui'

/**
 * Shadow me to add your own content
 */

const styles = {
  author: {
    display: `inline-block`,
    color: `alpha`,
  },
  occupation: {
    mb: 4,
  },
  specialty: {
    color: `text`,
    mb: 4,
  },
}

export default () => (
  <>
    <Heading variant='h1'>
      Welcome, I'm <Text sx={styles.author}>Nicolas</Text>.
    </Heading>
    <Heading variant='h1' sx={styles.occupation}>
      this blog talks about
    </Heading>
    <Heading variant='h3' sx={styles.specialty}>
      Geopolitic, books I've read, computer science and cybersecurity... More
      articles coming soon...
    </Heading>
    <Box variant='buttons.group'>
      <Button as={Link} to='/contact'>
        Contact Me
      </Button>
      <Button variant='white' as={Link} to='/about'>
        About Me
      </Button>
    </Box>
  </>
)
